* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}
body.body-pin {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .header-container {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .main-navigation {
    display: flex;
    gap: 2rem;
  }
}
.main-navigation a {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .main-navigation a {
    margin: 0;
  }
}
.main-navigation a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  z-index: 1;
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(255, 0, 213);
  background: linear-gradient(90deg, rgba(255, 0, 213, 0.3) 0%, rgba(139, 0, 255, 0.5) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
@media (min-width: 992px) {
  .video .overlay {
    text-align: left;
  }
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.video-content h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0 0 10rem 0;
}
@media (min-width: 992px) {
  .video-content h2 {
    margin: 0;
  }
}
.video-content p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.content-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .content-festival {
    margin-top: 0;
  }
}
.content-festival h2 {
  margin-bottom: 2rem;
}
.content-festival .date {
  color: #f53756;
  margin-bottom: 2rem;
  font-weight: 700;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .sceneries-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.sceneries-container p {
  color: #000;
}
.sceneries-container .scenery {
  padding-top: 2rem;
}
.sceneries-container .scenery p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .scenery-name:after {
  background-image: url("../img/icono_edm.png");
}

.rock .scenery-name:after {
  background-image: url("../img/icono_rock.png");
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.yellow-bg {
  background-color: #fef46d;
}
.yellow-bg li:nth-child(odd) {
  background-color: #fdec08;
}

.green-bg {
  background-color: #4cb8b3;
}
.green-bg li:nth-child(odd) {
  background-color: #3b9691;
}

.galery {
  padding: 5rem;
}
.galery h3 {
  text-align: center;
}

.images-galery {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.close-button {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #fff;
  color: #000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.close-button:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass {
  color: #000;
}
.pass .pass-name {
  text-align: center;
  font-size: 2.4rem;
  color: #fff;
}
.pass .header-pass, .pass .footer-pass {
  padding: 0.5rem;
}
.pass .header-pass p, .pass .footer-pass p {
  text-align: center;
  margin: 0;
}
.pass .header-pass {
  background-color: #f53756;
}
.pass .header-pass p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass .body-pass {
  background-color: #fff;
}
.pass .body-pass ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass .body-pass ul li,
.pass .body-pass ul :nth-child(odd) {
  background-color: #fff;
  padding: 0.5rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass .footer-pass {
  background-color: #752f97;
}
.pass .footer-pass p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #fff;
  font-size: 1.5rem;
}